splay:inline;position:relative;padding:0 5px}.display__section p{text-align:left;color:#517476}.display__section .tipSvg{-webkit-transform:scale(2);transform:scale(2);position:absolute;right:110%;top:38%}.display__section .tipSvg path{fill:#26c2ad}.display__section .amount h3{font-size:14px}.resetButton{width:80%;display:block;margin:150px auto 0 auto;height:50px;border-radius:5px;border:none;color:#00474b;font-size:24px;font-weight:700;background-color:#9fe8df;opacity:0.2}.resetButton.btn-active{background-color:#26c2ad;opacity:1;cursor:pointer}.resetButton.btn-active:hover{background-color:#9fe8df}@media screen and (max-width: 1400px){.input__section{padding-right:0px;margin-bottom:30px}.main{padding:20px}}@media screen and (max-width: 767px){.App{width:100%}.display__section .tipSvg{-webkit-transform:scale(1.8);transform:scale(1.8)}}

